<template>
  <n-modal
    v-model:show="show"
    :icon="getIcon()"
    preset="dialog"
    style="padding: 12px; width: 90vw; max-width: 600px"
  >
    <template #header>
      <span>å¯¼å…¥å®¹å™¨</span>
    </template>

    <div class="import-content">
      <n-form ref="formRef" :model="form" :rules="rules" label-placement="top">
        <n-grid :cols="24" :x-gap="12">
          <n-form-item-gi :span="12" label="é•œåƒåç§°" path="repository">
            <n-input v-model:value="form.repository" placeholder="ä¾‹å¦‚: myapp" />
          </n-form-item-gi>
          <n-form-item-gi :span="12" label="æ ‡ç­¾" path="tag">
            <n-input v-model:value="form.tag" placeholder="ä¾‹å¦‚: latest" />
          </n-form-item-gi>
        </n-grid>
      </n-form>

      <n-upload
        ref="uploadRef"
        :max="1"
        :file-list="fileList"
        :custom-request="handleCustomRequest"
        accept=".tar"
        :show-file-list="false"
        directory-dnd
        @update:file-list="handleFileListUpdate"
        @before-upload="handleBeforeUpload"
      >
        <n-upload-dragger>
          <div class="upload-area">
            <n-icon size="48" :depth="3">
              <CloudUploadOutline />
            </n-icon>
            <n-text class="upload-title"> ç‚¹å‡»æˆ–è€…æ‹–åŠ¨æ–‡ä»¶åˆ°è¯¥åŒºåŸŸæ¥ä¸Šä¼  </n-text>
            <n-p depth="3" class="upload-hint"> ä»…æ”¯æŒ .tar æ ¼å¼çš„ Docker å®¹å™¨æ–‡ä»¶ </n-p>
          </div>
        </n-upload-dragger>
      </n-upload>

      <!-- ä¸Šä¼ è¿›åº¦ -->
      <div v-if="uploading" class="upload-progress">
        <n-space vertical>
          <div class="progress-info">
            <n-text>{{ currentFileName }}</n-text>
            <n-text depth="3"
              >{{ formatFileSize(uploadedSize) }} / {{ formatFileSize(totalSize) }}</n-text
            >
          </div>
          <n-progress
            type="line"
            :percentage="uploadProgress"
            :show-indicator="false"
            :height="8"
          />
          <div class="progress-details">
            <n-text depth="3">{{ uploadProgress.toFixed(1) }}% - {{ uploadSpeed }}</n-text>
          </div>
        </n-space>
      </div>

      <!-- æ–‡ä»¶ä¿¡æ¯ -->
      <div v-if="selectedFile" class="file-info">
        <n-card size="small">
          <div class="file-detail">
            <n-icon size="20" :color="theme.primaryColor">
              <DocumentTextOutline />
            </n-icon>
            <div class="file-meta">
              <n-text class="file-name">{{ selectedFile.name }}</n-text>
              <n-text depth="3" class="file-size">{{ formatBytes(selectedFile.size) }}</n-text>
            </div>
          </div>
        </n-card>
      </div>
    </div>

    <n-alert type="info" class="mt-2">
      æ³¨æ„ï¼šå®¹å™¨ä¸Šä¼ åä¼šç”Ÿæˆå¯¹åº”çš„é•œåƒï¼Œè¯·ç”¨é•œåƒåœ¨ç»§ç»­å®¹å™¨çš„åˆ›å»º
    </n-alert>

    <template #action>
      <div class="modal-actions">
        <n-button @click="handleCancel" :disabled="uploading">
          {{ uploading ? 'ä¸Šä¼ ä¸­...' : 'å–æ¶ˆ' }}
        </n-button>
        <n-button
          type="primary"
          :loading="uploading"
          :disabled="!selectedFile || !form.repository || uploading"
          @click="handleImport"
        >
          {{ uploading ? 'å¯¼å…¥ä¸­...' : 'å¼€å§‹å¯¼å…¥' }}
        </n-button>
      </div>
    </template>
  </n-modal>
</template>

<script setup lang="ts">
import { formatBytes, renderIcon } from '@/common/utils'
import { useXhrUpload } from '@/hooks/useXhrUpload'
import { useSettingStore } from '@/store/setting'
import { CloudUploadOutline, DocumentTextOutline } from '@vicons/ionicons5'
import type { FormRules, UploadFileInfo } from 'naive-ui'
import { useMessage, useThemeVars } from 'naive-ui'
import { ref, watch } from 'vue'

const theme = useThemeVars()
const settingStore = useSettingStore()

const getIcon = () => {
  return renderIcon(CloudUploadOutline, {
    color: theme.value.primaryColor,
    size: 20,
  })
}
interface Emits {
  (e: 'success'): void
}

const show = defineModel<boolean>('show')
const emit = defineEmits<Emits>()

// å“åº”å¼æ•°æ®
const message = useMessage()
const formRef = ref()
const uploadRef = ref()
const fileList = ref<UploadFileInfo[]>([])
const selectedFile = ref<File | null>(null)

// ä½¿ç”¨ä¸Šä¼ hooks
const {
  uploading,
  uploadProgress,
  uploadedSize,
  totalSize,
  currentFileName,
  uploadSpeed,
  resetState,
  upload,
  formatFileSize,
} = useXhrUpload()

// è¡¨å•æ•°æ®
const form = ref({
  repository: '',
  tag: 'latest',
})

// è¡¨å•éªŒè¯è§„åˆ™
const rules: FormRules = {
  repository: {
    required: true,
    message: 'è¯·è¾“å…¥é•œåƒåç§°',
    trigger: ['input', 'blur'],
  },
}

// ç›‘å¬å¼¹çª—æ˜¾ç¤ºçŠ¶æ€
watch(show, (showModal) => {
  if (showModal) {
    resetState()
  }
})

const handleBeforeUpload = (data: { file: UploadFileInfo }) => {
  const file = data.file.file
  if (!file) {
    return false
  }

  // æ£€æŸ¥æ–‡ä»¶ç±»å‹
  if (!file.name.endsWith('.tar')) {
    message.error('ä»…æ”¯æŒ .tar æ ¼å¼çš„æ–‡ä»¶')
    return false
  }

  // æ£€æŸ¥æ–‡ä»¶å¤§å° (é™åˆ¶ä¸º 5GB)
  const maxSize = 5 * 1024 * 1024 * 1024 // 5GB
  if (file.size > maxSize) {
    message.error('æ–‡ä»¶å¤§å°ä¸èƒ½è¶…è¿‡ 5GB')
    return false
  }

  selectedFile.value = file
  return false // é˜»æ­¢è‡ªåŠ¨ä¸Šä¼ 
}

const handleFileListUpdate = (files: UploadFileInfo[]) => {
  fileList.value = files
  if (files.length === 0) {
    selectedFile.value = null
  }
}

const handleCustomRequest = () => {
  // é˜»æ­¢é»˜è®¤ä¸Šä¼ è¡Œä¸º
}

const handleImport = async () => {
  if (!selectedFile.value || !form.value.repository) {
    message.error('è¯·é€‰æ‹©æ–‡ä»¶å¹¶å¡«å†™é•œåƒåç§°')
    return
  }

  // è¡¨å•éªŒè¯
  try {
    await formRef.value?.validate()
  } catch {
    return
  }

  try {
    // åˆ›å»º FormData
    const formData = new FormData()
    formData.append('file', selectedFile.value)
    formData.append('repository', form.value.repository)
    formData.append('tag', form.value.tag)

    // ä½¿ç”¨å…¬å…±ä¸Šä¼ hooks
    await upload({
      url: '/api/v1/containers/import',
      file: selectedFile.value,
      formData,
      getToken: () => settingStore.getToken(),
      onSuccess: () => {
        message.success('å®¹å™¨å¯¼å…¥æˆåŠŸ')
        emit('success')
        handleCancel()
      },
    })
  } catch (error: any) {
    console.error('å¯¼å…¥å¤±è´¥:', error)
  }
}

const handleCancel = () => {
  if (!uploading.value) {
    show.value = false
    // é‡ç½®çŠ¶æ€
    resetState()
    selectedFile.value = null
    fileList.value = []
    form.value = {
      repository: '',
      tag: 'latest',
    }

    // æ¸…ç©º upload ç»„ä»¶
    uploadRef.value?.clear()
  }
}
</script>

